import React, { Component } from 'react'; // let's also import Component
import { ViewerControlSprings, XYType } from '../../common/types';
import { orbitList } from '../tansTheoremUtils';
import { AnimationStatus } from './MisiurewiczModeFragment';
import OrbitCard from './OrbitCard';
import OrbitMarker, { pointWithinBoundingBox } from './OrbitMarker';

// the clock's state has one field: The current time, based upon the
// JavaScript class Date
type OrbitManagerState = {
  currentPoint: XYType;
  period: number;
  prePeriod: number;
  orbitPoints: JSX.Element[];
};

type OrbitManagerProps = {
  mandelbrot: ViewerControlSprings;
  setAnimationState: React.Dispatch<React.SetStateAction<AnimationStatus>>;
  mapWidth: number;
  mapHeight: number;
};

export class OrbitManager extends Component<OrbitManagerProps, OrbitManagerState> {
  tick() {
    const ASPECT_RATIO = this.props.mapWidth / this.props.mapHeight;
    const orbitInformation = orbitList(
      [0, 0],
      this.props.mandelbrot.xyCtrl[0].xy.getValue(),
      20,
    );
    const orbitPs = orbitInformation[0].filter((x) =>
      pointWithinBoundingBox(
        x,
        this.props.mandelbrot.xyCtrl[0].xy.getValue(),
        ASPECT_RATIO / this.props.mandelbrot.zoomCtrl[0].z.getValue(),
        1 / this.props.mandelbrot.zoomCtrl[0].z.getValue(),
        -this.props.mandelbrot.rotCtrl[0].theta.getValue(),
      ),
    );
    this.setState({
      currentPoint: this.props.mandelbrot.xyCtrl[0].xy.getValue(),
      prePeriod: orbitInformation[1],
      period: orbitInformation[2],
      orbitPoints: [...Array(orbitPs.length).keys()].map((i) => {
        const p = orbitPs[i];
        const color =
          i + 1 > orbitPs.length - orbitInformation[2] ? '#EE0000' : '#EEEE00';
        return (
          <OrbitMarker
            key={p.toString()}
            iterate={p}
            mapWidth={this.props.mapWidth}
            mapHeight={this.props.mapHeight}
            show={true}
            mandelbrotControl={this.props.mandelbrot}
            color={color}
          />
        );
      }),
    });
  }

  UNSAFE_componentWillMount() {
    this.tick();
  }

  componentDidMount() {
    setInterval(() => this.tick(), 100);
  }

  // render will know everything!
  render() {
    return (
      <>
        {this.state.orbitPoints}
        <OrbitCard
          setAnimationState={this.props.setAnimationState}
          currentPoint={this.state.currentPoint}
          prePeriod={this.state.prePeriod}
          period={this.state.period}
        />
      </>
    );
  }
}

export default OrbitManager;
